@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';











































































































































































































































































































































































































































@import '~@assets/scss/theme.scss';

.user-info-mamager {
  .el-tree
    > .el-tree-node
    > .el-tree-node__children
    > .el-tree-node
    > .el-tree-node__content {
    height: auto;
  }
  .el-tree
    > .el-tree-node
    > .el-tree-node__children
    > .el-tree-node
    > .el-tree-node__children
    .el-tree-node
    > .el-tree-node__content {
    height: auto;
  }
  .el-tree-node > .el-tree-node__children {
    overflow: inherit;
  }
  .info-item {
    display: flex;
    align-items: center;
    width: 100%;
    .label {
      display: block;
      line-height: 26px;
      text-align: left;
      padding: 0 12px 0 0;
      box-sizing: border-box;
      .menu-title {
        display: inline-block;
        font-size: 14px;
      }
    }
    .content {
      position: relative;
      width: 100%;
      max-height: 100px;
      line-height: 32px;

      &.expand {
        max-height: none;
      }

      &-item {
        flex: 0 0 auto;
        padding: 2px 10px;
        margin-top: 20px;
        margin-right: 40px;
        border: 1px solid #ccc;
        border-radius: 5px;
      }

      &::before,
      &::after {
        display: table;
        content: '';
      }

      &::after {
        clear: both;
      }

      .box-col {
        height: 32px;
        margin-left: 20px;
        font-size: 14px;
        color: rgba(#4a4a4a, 0.6);
      }
    }

    &-add {
      cursor: pointer;
      display: inline-block;
      width: 22px;
      margin-left: 10px;
      text-align: center;
      border: 1px solid rgb(55, 149, 211);
      border-radius: 3px;
    }

    &:before,
    &:after {
      display: table;
      content: '';
    }

    &:after {
      clear: both;
    }
  }

  .info-wrap {
    .dataAuth {
      padding: 20px;
      padding-left: 0;
    }
    position: relative;

    &.user-info {
      height: 240px;
      overflow-y: auto;
      .user-info-avatar {
        display: inline-block;
        width: 60px;
        height: 60px;
        margin-top: 20px;
        margin-left: 20px;
        img {
          display: block;
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
        .head-portrait {
          float: left;
          display: block;
          width: 60px;
          height: 60px;
          border-radius: 50%;
          background: rgba(#30b3b3, 0.6);
          color: $main-background-color;
          text-align: center;
          line-height: 60px;
          margin-right: 8px;
          font-size: 32px;
        }
      }
      .user-info-name {
        display: inline-block;
        margin-left: 15px;
        margin-top: 25px;
        vertical-align: top;
        .user-name {
          font-size: 16px;
          font-weight: bold;
          margin-bottom: 0;
          margin-top: 0;
          span {
            font-weight: normal;
            margin-left: 10px;
          }
          .radius-tag {
            width: 68px;
            border-radius: 12px;
            text-align: center;
            font-size: 12px;
            &.active {
              color: #2bbcb8;
              background-color: rgba(43, 188, 184, 0.15);
            }
            &.locked {
              color: #cc3d3d;
              background-color: rgba(204, 61, 61, 0.15);
            }
            &.unActive {
              color: #eaa926;
              background-color: rgba(234, 169, 38, 0.15);
            }
          }
        }
        .user-email {
          height: 20px;
          color: #2a76cd;
          font-size: 14px;
          margin-top: 0;
        }
      }
      .user-info-detail {
        margin-left: 35px;
        margin-top: 5px;
        padding: 0;
        font-size: 14px;
        li {
          height: 32px;
          line-height: 32px;
          white-space: nowrap;
          list-style: none;
          .label {
            display: inline-block;
            width: 100px;
          }
        }
      }
    }

    .panel-nopadding {
      height: 280px;
      .panel-title {
        margin-bottom: 0;
      }

      .role-list-tree,
      .user-auth-wrap {
        &::-webkit-scrollbar {
          width: 8px;
        }
        &::-webkit-scrollbar-thumb {
          border-radius: 4px;
          background-color: #dbdcdc;
        }
      }
      .user-auth-wrap {
        padding: 10px 0;
        .table-panel {
          // height: 210px;
          margin-right: 20px;
          .inlineStyle {
            padding-top: 0 !important ;
          }
        }
        .base-panel {
          padding: 0 0 20px 20px !important;
          height: 190px;
          overflow-y: scroll;
        }
        line-height: 40px;
        .purple-tag {
          margin-right: 20px;
          &:last-child {
            margin-right: 0;
          }
        }
        .el-tabs__content {
          height: 150px;
          overflow: hidden;
          overflow-y: scroll;
          &::-webkit-scrollbar {
            width: 8px;
          }
          &::-webkit-scrollbar-thumb {
            border-radius: 4px;
            background-color: #dbdcdc;
          }
        }
      }
    }

    &.user-data-auth {
      .tag-wrap {
        height: calc(100vh - 470px);
        line-height: 40px;
        overflow: hidden;
        overflow-y: scroll;
        &::-webkit-scrollbar {
          width: 8px;
        }
        &::-webkit-scrollbar-thumb {
          border-radius: 4px;
          background-color: #dbdcdc;
        }
        .normal-tag {
          margin-right: 20px;
        }
      }
    }
  }
  .actions-list {
    height: 240px;
    border: 1px solid $color-table-cutting;
    padding: 10px 0;
    .el-tree-node__content > .el-tree-node__expand-icon {
      padding: 0;
    }
    .el-tree {
      overflow: scroll;
      height: 190px;
      width: 100%;
    }
  }
  .tag-action-tree {
    .el-tree-node__content {
      height: inherit;
    }
  }

  .el-checkbox + .el-checkbox {
    margin-left: 20px;
  }
  .el-checkbox__input.is-disabled.is-indeterminate {
    .el-checkbox__inner {
      background-color: #2a76cd;
      border-color: #2a76cd;
    }
  }
  .el-checkbox__input.is-disabled.is-checked {
    .el-checkbox__inner {
      background-color: #2a76cd;
      border-color: #2a76cd;
    }
  }
}
