// 02 颜色
// 主色
$main-bg-color: #fff;  // 主背景色
$logo-main-color: #CD9C15;  // LOGO
$layout-nav-color: linear-gradient(#104a90, #154e93, #104a90);  // 导航背景渐变色
$layout-subnav-color: #0A4184;  // 导航二级菜单展开背景色
// 辅助色
$button-selected-color: #2A76CD;  // 按钮/选中状态
$submenu-selected-color: rgba(42, 118, 205, 0.6);  // 二级菜单选中色
$prompt-floating-color: rgba(68, 75, 86, 0.95);  // 提示浮层
$auxiliary-icon-color: #B5B5B5;  // 辅助图标色
$table-border-color: #DBDCDC;  // 辅助线/表格边框 按钮描边/输入框描边
$table-split-color: #E8E8E5;  // 表格分割线
$thead-bg-color: #F3F4F6;  // 表头背景色
$table-hover-color: #F3F7FA;  // 表格hover状态时背景色
$base-forbidden-color: #F4F4F4;  // 禁用颜色
$base-bg-color: #F2F2F2;  // 背景颜色
// 状态用色
$state-process-color: #2A76CD;  // 进行中
$state-success-color: #44B365;  // 已完成
$state-warning-color: #EAA926;  // 提醒/新增
$state-error-color: #CC3D3D;  // 警示/异常
$state-pending-color: #CCC;  // 待处理/未开始
// 图表用色,透明度将为0.85（使用rgba函数实现）
$chart-region-color1: #2A76CD;
$chart-region-color2: #2BBCB8;
$chart-region-color3: #44B365;
$chart-region-color4: #EAA926;
$chart-region-color5: #CC3D3D;
$chart-region-color6: #8554C9;
$chart-region-color7: #3C3A9A;
$chart-region-color8: #3A4777;

// 03 字体
$font-family: "Microsoft YaHei", "SimSun", "PingFang SC", "Hiragino Sans GB", Helvetica Neue, Helvetica, Arial;
$base-font-color: #3C4455;  // 字体颜色
$auxiliary-text-color: rgba(60, 68, 85, 0.7);  // 次要说明性文字
$auxiliary-subtext-color: rgba(60, 68, 85, 0.4);  // 次要说明性文字
$link-text-color: #2A76CD; // 链接性文字
$font-size-base: 14px; // 正文文字(默认使用)、副标题文字(表头名称)
$font-size-lg: 18px; // 大标题
$font-size-md: 16px; // 主标题文字(卡片、表格标题)
$font-size-sm: 12px; // 正文文字
$base-line-height: 1.42857142857143;  //  基本行高

// TODO 赢时胜对布局导航、颜色、字体的变量
$text-color-help: $auxiliary-text-color;
$stroke-color: $table-border-color;
//  主色
$logo-front-color: #cd9c15;  //  logo 前景色
$layout-header-navi: #104a90;  //  头部导航背景
//  辅助色
$main-background-color: #f2f2f2;  //  主背景色
$button-selected-color: #2a76cd;  //  按钮/选中状态色
$layout-light-color: #dde3ea;  //  二级导航背景
$input-border: #dbdcdc;  //  按钮描边/输入框描边
$stroke-color: $input-border;
$card-background-color: #fdfdfd;  //  卡片背景
$alert-tip-color: rgba(68, 75, 86, 0.95);
//状态色
$disable-color: #f4f4f4;  // 禁用
$word-hyperlink-color: #068ce8;  // 文字超链接
$message-corner-color: #f1053f;  // 消息角标颜色
$primary-color: #3362a8;  // 选中项、默认按钮
$primary-gray-color: #c6c9cd;  // 图表、进度条、进程类灰色
// 状态色 -- 流程配色
$underway-color: #2a76cd;  //进行中
$success-color: #44b365;  //已完成
$warning-color: #eaa926;  //提醒/新增
$error-color: #cc3d3d;  //警示/异常
$notBegin-color: #cccccc;  //待处理/未开始
//  图表用色,透明度将为0.85（使用rgba函数实现）
$chart-region-color1: #2a76cd;
$chart-region-color2: #30b3b3;
$chart-region-color3: #44b365;
$chart-region-color4: #cd9c15;
$chart-region-color5: #cc3d3d;
$chart-region-color6: #8554c9;
$chart-region-color7: #3c3a9a;
$chart-region-color8: #3a4777;
//  字体
$font-family: 'Microsoft Yahei', 'HeiPingFang SC', 'Hiragino Sans GB', 'Helvetica Neue', 'Helvetica', 'Arial';
$base-font-color: #3c4455;  //  字体颜色
$text-color-help: rgba($base-font-color, 0.7);  // 辅助文字颜色
$text-color-link: #2a76cd; // 链接文字颜色
$font-size-base: 14px;
$font-size-md: $font-size-base + 2px;
$font-size-lg: $font-size-md + 2px;
$font-size-sm: $font-size-base - 2px;
$font-size-panel-title: $font-size-lg;
$font-size-card-title: $font-size-md;
//  排版
$base-line-height: 1.42857142857143;  //  基本行高
$box-shadow-base: 0px 2px 4px rgba(36, 100, 178, 0.1);  //  盒子阴影
//分割线及边框
$vertical-nav1-line: 1px solid #073572; // 垂直导航一级菜单分割线
$vertical-nav2-line: 1px solid #3362a8; // 垂直导航二级菜单分割线
$content-line: 1px solid #dee1e5; // 内容区及通用分割线
$dropDownMenu-line: 1px solid #d9d9d9; // 下拉菜单的描边
//  按钮
$base-button-height: 2em;  //  按钮高度
$button-disabled-color: #cccccc;
$button-disabled-background: #efefef;
$button-padding-lr: $font-size-base + 2px;
//  Layout
$layout-header-height: 48px;
$layout-sider1-width-open: 180px;
$layout-sider1-width-close: 48px;
$layout-sider2-width: 180px;
