// main
$color-main: #313236;
$color-white: #fff;
$color-headfont: #2A3451;
$color-main-hover: #DE680B;

// 表格颜色
$color-header: #18233D;
$color-body: #596580; ;
$color-table-border: #ebedf2;
$color-table-header:#EBECF0;
$color-table-bg: #f5f6fa;

// table样式
$stripe: #f2f5fa; // 斑马线
$border-color: #ebecf0; //表格边框
$row-split-color: #e8e8e5; //分割线
$font-size-base: 14px; // 正文文字(默认使用)、副标题文字(表头名称)
$header-color: #f3f4f6; //表头背景
$base-font-color: #3c4455;  //  字体颜色
$hover-row-color: #f3f7fa;  //行hover
$text-color-help: rgba($base-font-color, 0.7);  // 辅助文字颜色
$blue: #2a76cd;  //按钮颜色

// status color
$color-status-success: #0faf0f; // success
$color-status-warning: #e1af0f; // waring
$color-status-danger: #af0f0f; // danger

// text color
$color-mainfont: #4a4a4a; // main font
$color-icon: #929292; // icon输入框说明叙述文字
$color-border-hover: #b6b6b6; // hover边框
$color-button-text: #0D3F98;

// line color
$color-line-button: #dbdbdb;
$color-line-card: #ededed;
$color-line-nav: rgba($color-mainfont, 0.3);
$color-line-input: rgba($color-mainfont, 0.2);
$color-panel-boder: $color-line-button;

// background color
$color-body-bg: #F5F7FC;
$color-background: $color-body-bg;
$color-menu-hover: rgba($color-main, 0.1);
$color-button-bg: #C0D0EC;
$color-table-header-bg: #EBECF0;
:root {
  --color-back-nav: #091022; // 导航背景
  --color-back-click: #030A1C; // 二级/三级导航背景
  --color-menu-text-back: #3674f9; //
  --color-menu-default-text: #C6CCDA; // 导航默认文字
  --color-menu-check-text: #ffffff; // 导航选中文字
  --color-subtitle-text: #242633; // 大、小标题/表头
  --color-back-page: #F5F7FC; // 页面背景
  --color-back-header: #F5F7FC; // 表头背景
  --color-back-card: #ffffff; // 卡片背景
  --color-main: rgba(47,106,255,1); // 主色、品牌色
  --color-main-opcity: rgba(47,106,255,0.1); // 主色有透明度用于tab背景色
  --color-link-text: #2f6aff; // 链接文字
  --color-main-text: #474C5C; // 正文
  --color-form-hover: #F8F8F8; // 列表、表单hover背景
  --color-btn-hover: #618EFF; // 按钮hover状态
  --color-btn-click: #2554CC; // 按钮click状态
  --color-table-cutting: #EBECF0; // 表格描边、分割线
  --color-control: #E1E4EB; // 控件描边
  --color-icon: #BFC3CC; // icon颜色
  --color-Disable: #ACC3FF; // Disable状态
  --color-Success: rgba(111,180,35,1); // 成功
  --color-Warning: rgba(245,166,35,1); // 提醒
  --color-Error: rgba(223,81,70,1); // 警告、错误
  --color-Silent: #95A4BC; // 未启动
  --color-textassist: #95A4BC; // 辅助文字
  --color-textfailure: #C6CCDA; // 失效文字
  --color-Success-hover: rgba(111,180,35,.75); // 成功hover
  --color-Success-disable: rgba(111,180,35,.4); // 成功disable
  --color-Success-click: #58901c; // 成功click
  --color-Warning-hover: rgba(245,166,35,.75); // 提醒hover
  --color-Warning-disable: rgba(245,166,35,.4); // 提醒disable
  --color-Warning-click: #c4841c; // 提醒click
  --color-Error-hover: rgba(223,81,70,.75); // 警告、错误hover
  --color-Error-disable: rgba(223,81,70,.4); // 警告、错误disable
  --color-Error-click: #b24038; // 警告、错误click
  // --color-icon-click: #2554CC; // iconclick
  
}
