$color-back-nav: var(--color-back-nav);
$color-back-click: var(--color-back-click);
$color-menu-text-back: var(--color-menu-text-back);
$color-menu-default-text: var(--color-menu-default-text);
$color-menu-check-text: var(--color-menu-check-text);
$color-subtitle-text: var(--color-subtitle-text);
$color-back-header: var(--color-back-header);
$color-main: var(--color-main);
$color-main-opcity: var(--color-main-opcity);
